import React, {Component} from 'react';
import products from '../products.json';

export default class ProductView extends Component{

    state = {
        product: {}
    }

    componentDidMount(){
        const product = this.props.history.location.state.product;
        console.log(product);
        
        //console.log(this.props.history.location.state.productId);
    }

    render() {
        const {name, price, imageUrl} = this.props.history.location.state.product;
        return (
            <div style={{
                width: '100%',
                display: 'flex'
            }}>
                <div className='w-70 d-flex justify-content-center' style={{
                    display: 'inline-block'
                }}>
                    <div className='border-bottom w-75 p-3 mh- 30'>
                        <img src={imageUrl} class="img-fluid" style={{
                            width: 1000,
                            height: 400,
                            objectFit: 'contain'
                        }} alt='product'/>
                    </div>
                </div>
                <div className='border rounded' style={{
                    width: '30%',                     
                    display: 'inline-block'
                }}>
                    <h3>{name}</h3>
                    <h4>{`$ ${price}`}</h4>
                    <div className='w-100' style={{
                        textAlign: 'center',
                        fontWeight: 'bold'
                    }}>
                        <button style={{
                            width: '98%'
                        }} className='btn btn-primary'>Comprar</button>
                    </div>
                    
                </div>
            </div>
        );
    }
}