import React, {Component} from 'react';

export default class Container extends Component{

    state = {
        width: this.props.width ? this.props.width : '100%',
        bgcolor: this.props.bgcolor ? this.props.bgcolor : 'white',
        center: this.props.center ? this.props.center : 'false'
    }

    containerStyle = ()=>{
        const {width, bgcolor, center} = this.state

        let currentStyle = {};
        if(center === 'false'){
            currentStyle = {
                width: width,
                //backgroundColor: bgcolor,
                display: 'flex',
            }
        }else if(center === 'true'){
            currentStyle = {
                width: width,
                //backgroundColor: bgcolor,
                minHeight: 100,
                display: 'flex',
                justifyContent: 'center',
                flexWrap: 'wrap',
            }
        }
        return currentStyle;
    }

    render() {
        return (
            <div className='container'
                style={this.containerStyle()}
            >{this.props.children}</div>
        );
    }
}