import React, {Component} from 'react';
import defaultImageProduct from '../noproduct.png';

export default class CardItem extends Component{

  state = {
    routerContext: this.props.routerContext ? this.props.routerContext : '',
    productId: this.props.productId ? this.props.productId : 0,
    name: this.props.name ? this.props.name : 'Product',
    price: this.props.price ? this.props.price : '0.00',
    imageUrl: this.props.imageUrl ? this.props.imageUrl : defaultImageProduct,
    shadow: '',
    zindex: 'auto'
  }

  hasShadow = 'shadow p-3 mb-5 bg-white rounded';

  showSelect = () => {
    this.setState({shadow: this.hasShadow, zindex: '999'});
  }

  hideSelect = () => {
    this.setState({shadow: '', zindex: 'auto'});
  }

  clickProduct = ()=>{
    const {productId, name, price, imageUrl} = this.state;
    this.props.routerContext.push({pathname: '/product', state: {product:{
      id: productId,
      name: name,
      price: price,
      imageUrl: imageUrl
    }}})
  }

  render() {
    const {name, price, imageUrl, shadow, zindex} = this.state;
    return (
      <div onClick={this.clickProduct} style={{
        width: '18%',
        display: 'inline-block',
        marginBottom: 10,
        marginInlineEnd: 5,
        padding: 5,
        objectFit: 'contain',
        cursor: 'pointer',
        height: 310,
        zIndex: zindex,
        minWidth: 250
      }}>
          <div className={`card ${shadow}`} onMouseOver={this.showSelect} onMouseOut={this.hideSelect} style={{
              width: '100%'
          }}>
          <img className="card-img-top" 
          style={{objectFit: 'contain'}}
          src={imageUrl} 
          alt="product" width='200' height='200'/>
          <br/>
          <ul className="list-group list-group-flush">
            <li className="list-group-item">
              <h3 style={{
                fontSize: '1.2vw'
              }}>{`$${price}`}</h3>
              <div style={{fontWeight: 'normal', color: 'gray'}}>
                {this.state.shadow === this.hasShadow ? `${name.substring(0, 30)}...` : ''}
              </div>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}