import React, {Component} from 'react';
import MarketLogo from '../mllogo.png';

export default class Logo extends Component{

    state = {
        width: this.props.width ? this.props.width : '30%',
        display: this.props.display ? this.props.display : 'none'
    }

    render() {

        const {width, display} = this.state;

        return (
            <img src={MarketLogo} alt='logo' style={{
                width: width,
                display: display,
                //backgroundColor: 'red',
                objectFit: 'contain'
            }}/>
        );
    }
}