import React, {Component} from 'react';
import { FaSearch } from 'react-icons/fa';


export default class SearchBar extends Component{

    state = {
        width: this.props.width ? this.props.width : '40',
        display: this.props.display ? this.props.display : 'none',
        placeholder: 'Buscar productos, marcas y más'
    }

    render() {
        const {width, display, placeholder} = this.state;
        return (
            <div className="form-inline d-flex justify-content-center md-form form-sm" style={{
                    //backgroundColor: 'green',
                    width: '50%'
                }}>
                <input className={`form-control form-control-sm mr-3 w-40`} type="text" placeholder={placeholder} aria-label="Search" style={{width: '85%'}}/>
                <i aria-hidden="true" style={{width: '10%'}}>
                    <FaSearch/>
                </i>
            </div>
        );
    }
}